.ll_level-selector{
  overflow:hidden;
  background: transparent;
  font-family: $LLTypography;
  font-weight: $Light-Type;
  @extend .fit-to-screen;
  z-index: $Layer-Level-5;
  @include transition(background .5s ease);
  -webkit-font-smoothing: antialiased;
  &.ll_level-selector-in{
    background: rgba(0, 0, 0, 0.6);
  }
}

.ll_level-selector-body{
  bottom: -100%;
  background: $LL-White;
  padding:20px;
  position:absolute;
  box-sizing: border-box;
  width: 100%;
  z-index: $Layer-Level-6;
  height: auto;
  overflow: auto;
  @include transition(bottom .5s ease);
  -webkit-overflow-scrolling: touch;
}

.ll_level-selector-in {
  .ll_level-selector-body{
    bottom:0;
    animation: showUp .5s ease;
  }
}

.ll_level-selector-title{
  @include ellipsis();
  color: lighten($LL-Gray, 6);
  display: block;
  font-size: $LL-Huge-Font;
  font-weight: $Normal-Type;
  line-height: 25px;
  margin:0 0 15px 0;
  width: 95%;
  .ll_icon{
    font-size: $LL-Large-Font;
    margin-right: 19px;
    vertical-align: inherit;
  }
}

.ll_level-selector-list{
  padding: 0;
  .ll_level-selector-item:last-child{
    margin-bottom: 0;
  }
}

.ll_level-selector-item{
  cursor: pointer;
  display: block;
  margin-bottom: 15px;
  padding-left: 40px;
  position: relative;
  .ll_icon{
    color: $LL-Red;
    font-size: $LL-Medium-Font;
    left: 0;
    position: absolute;
    top: 4px;
  }
}

.ll_level-selector-item-title{
  @include ellipsis();
  display: block;
  color: $LL-Blue;
  font-size: $LL-Huge-Font;
  font-weight: $Normal-Type;
  line-height: 26px;
  width: 95%;
}

.ll_level-selector-item-icon {
  display: none;
  .ll_level-selector-item--is-active &{
    display: block;
  }
}

.ll_level-selector-item-details{
  display: block;
  font-size: $LL-Normal-Font;
  font-weight: $Light-Type;
  color: $LL-Gray;
}

.ll_level-selector-item-badge{
  font-weight: $Light-Type;
  font-size: $LL-Tiny-Font;
  display: inline-block;
  border-radius: 2px;
  margin-bottom: 8px;
  padding: 2px 4px;
  width: auto;
}

.ll_level-selector-item-badge-primary{
  background: $LL-Purple;
  color: $LL-White;
}

.ll_level-selector-item-badge-secondary{
  background: $LL-Red;
  color: $LL-White;
}

@keyframes showUp {
  from {
    bottom: -230px;
  }
  to {
    bottom: 0;
  }
}