@import 'variables';

//Layout Extensions

.fit-to-screen{
  //position: fixed;
  position:absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

// Typographic Extensions

.ll_standard-typography{
  font-family: $LLTypography;
  font-size: $LL-Normal-Font;
  font-weight: $Normal-Type
}

.ellipsis{
  box-sizing: border-box;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.disabled{
  color:$LL-LightGray !important;
}

.active{
  color:$LL-Green !important;
}