Instructions: Place your Sass mixins into this sheet. Then call import on your variables and mixins for each Sass Module
Hierarchy:    Main.scss --> _ModuleName.scss --> _Utilities.scss
Categories: Mathematics, Animations, Element Modification
==================================*/

//////////////////////////////
///// COMBINATOR MIXINS /////
////////////////////////////

@mixin whmp($width,$height,$margin,$padding) {
  width: $width;
  height: $height;
  margin: $margin;
  padding: $padding;
}

/////////////////////////////////
/////// MATHEMATICS MIXINS /////
///////////////////////////////

//Ex: @include calc(width,"100% - 16px");
@mixin calc($property, $expression) {
  #{$property}: -webkit-calc(#{$expression});
  #{$property}: -moz-calc(#{$expression});
  #{$property}: calc(#{$expression});
}

//Ex: @include calc-width("100px","10px"); = 10px
@mixin calc-width($numerator, $denominator) {
  width: ((#{$numerator}) / (#{$denominator}));
}

/////////////////////////////////
//////// ANIMATION MIXINS //////
///////////////////////////////

@mixin animation($transition...) {
// Shorthand is as follows: animation-name,animation-duration,animation-timing-function,animation-delay,animation-iteration-count,animation-direction,animation-fill-mode,animation-play-state
  -webkit-animation: $transition;
  -moz-animation: $transition;
  -o-animation: $transition;
  -ms-animation: $transition;
  animation: $transition;
}

//Ex: @include transition(background-color 300ms ease-in-out);
@mixin transition($transition...) {
  -moz-transition:    $transition;
  -o-transition:      $transition;
  -webkit-transition: $transition;
  -ms-transition:     $transition;
  transition:         $transition;
}
@mixin transition-property($property...) {
  -moz-transition-property:    $property;
  -o-transition-property:      $property;
  -webkit-transition-property: $property;
  -ms-transition-property:     $property;
  transition-property:         $property;
}
@mixin transition-duration($duration...) {
  -moz-transition-duration:    $duration;
  -o-transition-duration:      $duration;
  -webkit-transition-duration: $duration;
  -ms-transition-duration:     $duration;
  transition-duration:         $duration;
}
@mixin transition-timing-function($timing...) {
  -moz-transition-timing-function:    $timing;
  -o-transition-timing-function:      $timing;
  -webkit-transition-timing-function: $timing;
  -ms-transition-timing-function:     $timing;
  transition-timing-function:         $timing;
}
@mixin transition-delay($delay...) {
  -moz-transition-delay:    $delay;
  -o-transition-delay:      $delay;
  -webkit-transition-delay: $delay;
  -ms-transition-delay:     $delay;
  transition-delay:         $delay;
}

@mixin transform($transform...){
  -webkit-transform: $transform;
  -moz-transform:    $transform;
  -ms-transform:     $transform;
  -o-transform:      $transform;
  transform:         $transform;
}

//EX: @include keyframes( example )
//{
//  0% {
//    color: #fff;
//    @include transform( scale(1) );
//  }
//
//  100% {
//    color: #000;
//    @include transform( scale(2) );
//  }
//}
@mixin keyframes( $animationName )
{
  @-webkit-keyframes #{$animationName} {
    @content;
  }
  @-moz-keyframes #{$animationName} {
    @content;
  }
  @-o-keyframes #{$animationName} {
    @content;
  }
  @keyframes #{$animationName} {
    @content;
  }
}

/////////////////////////////////////
///// ELEMENT MODIFYING MIXINS /////
///////////////////////////////////

@mixin box-shadow($shadows...) {
  -moz-box-shadow: $shadows;
  -webkit-box-shadow: $shadows;
  box-shadow: $shadows;
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  -ms-border-radius: $radius;
  border-radius: $radius;
}

@mixin filter($property...) {
  -moz-filter: $property;
  -webkit-filter: $property;
  -o-filter: $property;
  -ms-filter: $property;
  filter: $property;
}

//////////

@mixin ellipsis(){
  box-sizing: border-box;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
