.ll_direction-wrapper{
  background: lighten($LL-Gray, 49);
  @extend .fit-to-screen;
  z-index: $Layer-Level-4;

  -webkit-font-smoothing: antialiased;
  &, input {
    font-family: $LLTypography;
    font-weight: $Light-Type;
  }
}

.ll_direction-box{
  @extend .cf !optional;
  background: $LL-DarkGray;
  padding: 0px 20px 5px 25px;
  position: relative;
}

input.ll_direction-input-text{
  color: $LL-White;
  background: none;
  box-sizing: border-box;
  border: 0;
  font-size: $LL-Medium-Font;
  font-weight:$Normal-Type;
  height: 40px;
  margin-bottom: 0;
  padding: 5px 64px 5px 0;
  width: 100%;
  &[placeholder],&::-webkit-input-placeholder,&:-moz-placeholder,&::-moz-placeholder,&:-ms-input-placeholder {
    @include ellipsis();
    color: lighten($LL-Gray, 11);
    font-weight:$Normal-Type;
  }
  &:focus{
    outline: none;
  }
}

.ll_direction-icons{
  color: $LL-Ghost;
  float: none;
  margin-right: 30px;
  position: absolute;
  left: 28px;
  top: 11px;
  i{
    display: inline-block;
    font-size: 57px;
    padding-top: 2px;
  }
}

.ll_direction-form{
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0 30px 0 40px;
  width: 100%;
  & .ll_direction-start-form,.ll_direction-end-form{
    position:relative;
  }
  & .ll_icon-clear-field {
    position: absolute;
    top: 0;
    right: 0;
    margin: 8px;
  }
}

.ll_direction-switch{
  color: $LL-Ghost;
  float: none;
  margin-left: 0;
  position: absolute;
  right: 20px;
  top: 24px;
  i{
    cursor: pointer;
    display: inline-block;
    font-size: $LL-Huge-Font;
    padding-top: 0;
  }
}

.ll_direction-separator{
  border-bottom: 1px solid $LL-White;
}

.ll_direction-bottom-bar {
  background-color: $LL-Blue;
  height: 48px;
  bottom: 0;
  position: absolute;
  cursor: pointer;
  width: 100%;
  z-index:$Layer-Level-1;
    & span {
      @extend .ll_standard-typography;
      float: right;
      color: $LL-White;
      text-transform: uppercase;
      margin: 15px 35px 15px;
    }
  }