.ll_title-bar{
  box-sizing: border-box;
  background: $LL-DarkGray;
  color: $LL-White;
  padding: 15px 20px 15px 25px;
  width: 100%;
  z-index: $Layer-Level-4;
}

.ll_title-bar-back{
  font-size: $LL-Normal-Font;
  position: absolute;
  color: $LL-White;
  text-decoration: none;
  cursor: pointer;
  padding: 5px;
  left: 20px;
}

.ll_title-bar-title{
  @include ellipsis();
  font-size: $LL-Large-Font;
  font-weight:$Normal-Type;
  padding-left: 40px;
  width: 100%;
}