.ll_dialog-container{
  font-family: Roboto;
  height: 200px;
  width: 320px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  background-color: white;
  border-radius: 3px;
  z-index: 100;
  margin: auto;
  display: block;
  box-shadow: #666 1px 2px 4px 1px;
}
.ll_dialog-header{
  margin: 16px 16px 8px 16px;
  font-size: 21px;
  color:$LL-Green;
  font-weight:$Medium-Type;
}
.ll_dialog-close{
  position: absolute;
  bottom: 16px;
  right: 16px;
  font-weight:$Ultra-Bold-Type;
  color:$LL-Blue;
  cursor: pointer;
}
.ll_dialog-text{
  padding:8px 16px;
  font-size:16px;
  color:$LL-Gray;
  line-height:24px;
}