.ll_navigation-box{
    font-weight: $Thin-Type;
    background: $LL-DarkGray;
    border-radius: 3px;
    box-sizing: initial;
    font-family: $LLTypography;
    -webkit-font-smoothing: antialiased;
    position:absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom:0;
    z-index: $Layer-Level-4;
}

.ll_navigation-box__background {
    background-color: $LL-White;
    @extend .fit-to-screen;
    z-index: $Layer-Level-3;
}

.ll_navigation-box__title {
    color: $LL-White;
    font-size: $LL-Large-Font;
    text-align: left;
    padding: 20px 0;
    line-height: 20px;
    font-weight: $Medium-Type;
    border-bottom: 1px solid lighten($LL-Gray, 11);
    .ll_navigation-box__back-button {
        margin: 0 0 0 22px;
        width:17px;
        position:absolute;
    }
    span{
        margin-left:72px;
    }
}

.ll_navigation-box__summary {
    @include ellipsis();
    color: white;
    text-align: left;
    margin-left:72px;
    padding: 5px 0;
    font-weight:$Medium-Type;
    font-size:$LL-Small-Font;
}

.ll_navigation-box__info{
    color: $LL-White;
    font-size: $LL-Small-Font;
    margin-bottom: 10px;
    margin-top: 0;
    text-align: center;
}

.ll_navigation-box__location{
    color: $LL-White;
    text-align: center;
    span{
        @include ellipsis();
        display: inline-block;
        font-size: $LL-Normal-Font;
        text-align: left;
        vertical-align: middle;
        width: 185px;
    }
    .icon{
        box-sizing: initial;
        display: inline-block;
        background: $LL-Green;
        border-radius: 4px;
        height: 25px;
        margin-right: 15px;
        padding: 4px 4px;
        vertical-align: middle;
        font-size: $LL-Medium-Font;
        width: 25px;
        &:before{
            vertical-align: middle;
            text-align: center;
        }
    }
}

.ll_navigation-box__show-link{
    color: $LL-White;
    font-size: $LL-Tiny-Font;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5px 0;
    position: absolute;
    text-align: center;
    text-decoration: none;
    .ll_icon{
        display: inline-block;
        font-size: 9px;
        margin-right: 8px;
        transform: rotateZ(90deg);
    }
    &.active{
        .ll_icon{
            transform: rotateZ(-90deg);
        }
        .ll_navigation-box__hide-message{
            display: inline-block;
        }
        .ll_navigation-box__show-message{
            display: none;
        }
    }
}

.ll_navigation-box__hide-message,
.ll_navigation-box__show-message{
    font-size: $LL-Tiny-Font;
}

.ll_navigation-box__hide-message{
    display: none;
}

.ll_navigation-box__show-message{
    display: inline-block;
}

.ll_navigation-box__steps{
    background: $LL-White;
    margin: 0 auto;
    overflow: scroll;
    padding: 0;
    position:absolute;
    top: 90px;
    right: 0;
    bottom: 0;
    left: 0;
    height:auto;
    li{
        border-bottom:1px solid $LL-Ghost;
        &:last-child{
            border-bottom: 0;
        }
    }
    &.hide{
        display: none;
    }
}


.ll_navigation-box__steps-item{
    display: block;
    height: 54px;
    padding: 10px 20px 10px 0;
    cursor: pointer;
    &.selected{
        background:darken( $LL-White, 12%);
    }
    .ll_media__img{
        margin-right: 15px;
        height: auto;
        bottom: 0;
        display: block;
        vertical-align: bottom;
        .ll_navigation-box__steps-icon{
            display: table-cell;
            height: 54px;
            width:59px;
            text-align: right;
            vertical-align: bottom;
            img{
                display: inline-block;
                vertical-align: bottom;
                height: 75%;
                width: auto;
            }
            .ll_navigation-box__steps-pin{
                height: 65px;
            }
        }
    }
    .ll_media__body{
        .ll_navigation-box__steps-info{
            @include ellipsis();
            @extend .ll_standard-typography;
            color: $LL-Gray;
            display: block;
            margin-bottom: 12px;
        }
        .ll_navigation-box__steps-title{
            @include ellipsis();
            font-weight:$Medium-Type;
            color: $LL-DarkGray;
            font-size: $LL-Large-Font;
            line-height: 19px;
            margin: 0;
            width:  100%;
        }
    }
}