.ll_bottom-bar {
  position:absolute;
  z-index: $Layer-Level-1;
  bottom: 0;
  height: 0;
  width: 100%;
  background-color: white;
  overflow-y: hidden;
  display:flex;
  z-index:$Layer-Level-1;
}


.ll_bottom-bar-item {
  //This should be utilizing a fixed width for older devices and a dynamic width for newer devices.
  height: 100%;
  float: left;
  text-align: center;
  margin:0 auto;
  // http://lea.verou.me/2011/01/styling-children-based-on-their-number-with-css3/
  @for $i from 1 through 20 {
    &:first-child:nth-last-child( #{$i} ),
    &:first-child:nth-last-child( #{$i} ) ~ & {
      width: unquote((100 / $i + "%"));
    }
  }
}

.ll_bottom-bar-item-icon {
  margin-top: 8px;
  height:22px;
  width:22px;
}

.ll_bottom-bar-item-text {
  bottom: 4px;
  left: 0;
  right: 0;
  font-family: $LLTypography;
  font-size: $LL-Tiny-Font;
  font-weight: $Normal-Type;
  text-transform: capitalize;
  color:$LL-Gray;
}