@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,300,700);

.ll_media{
  display:block;
  @extend .cf !optional;
}
  .ll_media__img{
    float:left;
    margin-right:20px;
  }
  /**
   * Reversed image location (right instead of left).
   */
  .ll_media__img--rev{
    float:right;
    margin-left:20px;
  }

    .ll_media__img img,
    .ll_media__img--rev img{
      display:block;
    }

  .ll_media__body{
    overflow:hidden;
  }
    .ll_media__body,
    .ll_media__body > :last-child{
      margin-bottom:0;
    }


  /**
   * `.img`s in `.islet`s need an appropriately sized margin.
   */
  .islet .ll_media__img{
    margin-right:10px;
  }
  .islet .ll_media__img--rev{
    margin-left:10px;
  }