.ll_tool-tip {
  width: 170px;
  background-color: black;
  color:white;
  opacity: 0.8;
  height: 50px;
  position: absolute;
  bottom: 0;
  right: 50px;
  padding: 9px;
  box-sizing: border-box;
  margin-right: 22px;
  border-radius:3px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.2);
  font-family: Roboto;
  & span.ll_tool-tip-number {
    color: white;
    font-size: 35px;
    line-height:35px;
    margin-right: 9px;
  }
  & span.ll_tool-tip-text {
    vertical-align: top;
    display: inline;
    float: right;
    position: absolute;
    font-size: 16px;
    line-height:16px;
    font-weight: 100;
  }
  &:before {
    position: absolute;
    z-index: -1;
    top: calc(0% + 15px);
    left: calc(100% - 10px);
    background-color: black;
    transform: rotate(45deg);
    width: 20px;
    height: 20px;
    content: "";
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.2);
  }
}


