$LLTypography: 'Roboto', Arial, sans-serif;


// Font-Weight Variables - Matches the Roboto's type weight terminology.
$Thin-Type:      100;
$Light-Type:     300;
$Normal-Type:    400;
$Medium-Type:    500;
$Bold-Type:      700;
$Ultra-Bold-Type:900;

//Font-Sizes - Determined from preexisting averages and design specs.
$LL-Tiny-Font:  12px;
$LL-Small-Font: 14px;
$LL-Normal-Font:16px;
$LL-Medium-Font:18px;
$LL-Large-Font: 21px;
$LL-Huge-Font:  24px;