.ll_search-bar{
    position: relative;
    -webkit-font-smoothing: antialiased;
    padding-top:72px;
    &, input {
        font-family: $LLTypography;
        font-weight: $Normal-Type;
    }
}

.ll_search-bar-field-region{
    margin: 8px;
    position:absolute;
    top: 8px;
    width: 98%; //Legacy Support
    @include calc(width,"100% - 16px");
    z-index: $Layer-Level-7;
    & .ll_search-field{
        background: $LL-White;
        border: none;
        border-radius: 2px;
        @include box-shadow(0 3px 3px rgba(0, 0, 0, 0.4));
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        font-size: $LL-Large-Font;
        font-weight:$Normal-Type;
        color: $LL-Gray;
        outline: none;
        padding: 14px 15px;
        width: 100%;
        @include transition-duration(400ms);
        @include transition-property(all);
        &:focus{
            padding: 14px 64px;
        }
    }
    & .ll_icon-clear-field {
        position: absolute;
        right: 32px;
        top:15px;
    }
    & .ll_title-bar-back {
        color: $LL-Black;
        position: absolute;
        top:13px;
    }
}

.ll_search-bar-container.active{
    @extend .fit-to-screen;
    overflow-y:scroll;
    padding-right: 16px; //Hide Scrollbar Hack
    width: 100%;
    height: 100%;
    z-index: $Layer-Level-4;
    background-color: lighten($LL-Gray, 49);
    & .ll_search-field{
        padding: 14px 64px;
    }
}

.ll_search-bar-container{
    background:none;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    position:absolute;
    z-index: $Layer-Level-1;
    @include transition-duration(120ms);
    @include transition-timing-function(cubic-bezier(.1,.55,.85,.26));
    @include transition-property(all);
}

.ll_search-bar-lists-region, .ll_direction-results-region{
    margin:8px;
    & .ll_search-results{
        @include box-shadow(0px 0px 8px 0px rgba(153,153,153,1));
        background: $LL-White;
        border-radius: 2px;
        margin-bottom: 12px;
        box-sizing: border-box;
    }
}

.ll_search-results-list {
    background: $LL-White;
    list-style-type: none;
    margin: 0;
    padding-left: 0;
}

.ll_search-results-item {
    line-height: normal;
    border-bottom: 1px solid saturate(lighten($LL-Gray, 38), 1); //#c8c7cc;
    color: $LL-DarkGray;
    cursor: pointer;
    display: block;
    font-size: $LL-Medium-Font;
    font-weight:$Normal-Type;
    padding: 6px 7px 6px 0;
    position: relative;
    margin-left: 48px;
    &:last-child{
        border-bottom: 0;
    }
    &:before{
        font-family: 'locuslabsdk';
        content: "\e62d";
        color: $LL-LightGray;
        font-size: $LL-Medium-Font;
        left: -33px;
        top: 10px;
        position: absolute;
    }
    .ll_search-autocomplete-results-list &{
        border: none;
    }
    .ll_search-autocomplete-results-list &:before{
        content: '\e648';
        font-size: $LL-Small-Font;
    }
}

.ll_search-results-empty-state {
    margin: 0;
    padding: 7px 15px;
}

.ll_search-results-title{
    color: $LL-Gray;
    font-weight: $Medium-Type;
    display: block;
    font-size: $LL-Small-Font;
    padding: 7px 15px;
}

.ll_search-results-item-name{
    color: darken($LL-Gray, 17);
    display: block;
    font-size: $LL-Medium-Font;
    font-weight: $Normal-Type;
    & small{
        color: $LL-Gray;
        font-size: $LL-Small-Font;
        margin-left: 6px;
    }
}

.ll_search-results-item-location{
    @extend .ll_standard-typography;
    color: $LL-Gray;
}

.ll_search-results-wrapper{
    margin: 10px;
    position: absolute;
    top:144px;
    bottom: 39px;
    width: 97.5%; // Jellybean support
    @include calc(width,"100% - 16px");
    overflow-y: scroll;
}

.ll_navigation-top-bar {
    position:absolute;
    top: 0;
    height: 0;
    width: 100%;
    background-color: $LL-DarkGray;
    font-family: $LLTypography;
    -webkit-font-smoothing: antialiased;
    z-index:$Layer-Level-4;
}

.ll_navigation-top-bar-instruction {
    font-weight: $Normal-Type;
    text-align: center;
    color: $LL-White;
}

.ll_navigation-top-bar-first-row {
    @include ellipsis();
    text-align: center;
    margin: 5px 0 0 0;
    line-height: $LL-Small-Font;
    font-size: $LL-Small-Font;
    width:100%;
    padding:0 30px;
    color: $LL-White;
}

.ll_navigation-top-bar-second-row {
    @include ellipsis();
    text-align: center;
    width: 100%;
    padding: 12px 45px 12px 45px;
    height: 54px;
    display: block;
    & i {
        &:before{
            background-position: 50%;
            height: 30px;
            width: 45px;
            content: "";
            background-size: cover;
            display: block;
        }
    }
}

.ll_navigation-top-bar-second-row-title {
    font-size: $LL-Large-Font;
    font-weight: $Normal-Type;
    color: $LL-White;
    margin-left:16px;
    vertical-align: middle;
}

.ll_navigation-top-bar-second-row-icon {
    height: 30px;
    font-size: 30px;
    vertical-align: middle;
    display: inline-block;
}

.ll_navigation-top-bar-total-distance {
    @include ellipsis();
    background-color: $LL-DarkGray;
    color: $LL-White;
    text-align: center;
    border-top: 1px solid $LL-LightGray;
    font-weight: $Normal-Type;
    font-size: $LL-Small-Font;
    width:100%;
    padding:5px 30px;
    line-height: 14px;
}

.ll_navigation-top-bar-show-summary-box {
    position:absolute;
    float:right;
    right:8px;
    height:24px;
    width:24px;
}