// Generic Card Styles
#locuslabs-zoom-container{
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position:absolute;
  overflow:visible;
  & > svg{
    overflow:hidden !important;
  }
}
.ll_card{
  background-color: $LL-White;
  @include box-shadow(0 0 10px rgba(0,0,0,0.4));
  bottom:0;
  left:0;
  right:0;
  position:absolute;
  width: auto;
  margin: 0;
  height: auto;
  -webkit-font-smoothing: antialiased;
  font-family: $LLTypography;
  font-weight: $Light-Type;
  &:after{
    content:'';
    height: 0;
    border-top: 15px solid transparent;
    border-right: 20px solid $LL-White;
    border-bottom: 15px solid transparent;
    left: -20px;
    position: absolute;
    top: 345px;
    width: 0;
  }
  & .ll_section{
    border-bottom: 1px solid $LL-Ghost;
    padding: 16px;
    & .ll_head{
      width:100%;
      & .ll_trigger{
        right: 12px;
        top: 20px;
        position: absolute;
        width: 14px;
        height:14px;
        font-size: $LL-Small-Font;
        color: $LL-Blue;
        &:before{
          @include transform(rotate(-90deg));
          transition-duration: 300ms;
          transition-property: all;
          position: absolute;
        }
      }
      & .ll_trigger.enabled{
        &:before{
          @include transform(rotate(90deg));
        }
      }
      .ll_flight-type{
        display:inline-block;
        width: 42px;
        height: 42px;
        margin-right:12px;
        vertical-align:top;
      }
      & .ll_content{
        display:inline-block;
        width:75%;
        @include ellipsis();
        & .ll_header{
          @include ellipsis();
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          font-size: $LL-Large-Font;
          color: $LL-Black;
          font-weight:$Normal-Type;
          width: 100%;
          text-transform: capitalize;
        }
        & .ll_list{
          & .ll_list-item{
            color: $LL-Gray;
            font-size: $LL-Normal-Font;
            font-weight: $Normal-Type;
            line-height: 16px;
            margin-top:8px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            & i{
              margin-right:14px;
              font-size:$LL-Normal-Font;
              &:before {
                color: $LL-Gray;
                font-size:$LL-Normal-Font;
              }
            }
          }
        }
      }
    }
    & .ll_button-bar{
        text-align:center;
      & .ll_button{
        height: 16px;
        width:auto;
        background: $LL-Blue;
        font-weight:$Medium-Type;
        font-size:$LL-Small-Font;
        border-radius: 2px;
        padding: 10px 16px;
        margin: 0 16px;
        display: inline-block;
        & .ll_button-text{
          text-transform: uppercase;
          display:inline-block;
          margin-left:8px;
          color: $LL-White;
          text-decoration: none;
        }
        & .ll_icon {
          height: 16px;
          width: 18px;
          font-size: 16px;
          &:before{
            height: 16px;
            width: 18px;
            display: inline-block;
          }
        }
      }
    }
    & .ll_operating-airline-logo{
      width: 50px;
      height: 50px;
      float: left;
      left: 10px;
      margin-top: 50px;
    }
    & .ll_body{
      margin-left:54px;
      line-height:24px;
      & .ll_header{
        font-size: $LL-Medium-Font;
        color: $LL-Green;
        font-weight: $Normal-Type;
        text-transform: uppercase;
      }
      & .ll_current-airport{
        margin-top:24px;
        font-size:$LL-Medium-Font;
        & .ll_current-airport-secondary-time{
          color:$LL-Red;
        }
        & .ll_current-airport-name{
          font-weight:$Medium-Type;
        }
      }
      & .ll_connecting-airport{
        margin-top:24px;
        font-size:$LL-Medium-Font;
        & .ll_connecting-airport-secondary-time{
          color:$LL-Red;
        }
        & .ll_connecting-airport-name{
          font-weight:$Medium-Type;
        }
        & .ll_connecting-airport-gate,& .ll_baggage-claim{
          & .ll_data-info{
            text-transform: capitalize;
            font-weight:$Medium-Type;
            color:$LL-Blue;
          }
        }
      }
    }
  }
  // POI Specific Card Styles
  &.ll_poi-box{
    & .ll_section{
      & .ll_services-navigation{
        border-top: 1px solid #ccc;
        position: relative;
        height: 40px;
        margin-top: 8px;
      }
      & .ll_poi-category{
        background-color:$LL-DarkGray;
        color: white;
        height:39px;
        width:39px;
        border-radius: 8px;
        font-size: 30px;
        display: inline-block;
        margin-right: 12px;
        vertical-align: top;
        &.ll_icon-relax{
          background-color:$LL-Red;
        }
        &.ll_icon-shop{
          background-color:$LL-Blue;
        }
        &.ll_icon-eat{
          background-color:$LL-Green;
        }
        &.ll_icon-restroom{
          background-color:$LL-Yellow;
        }
        &.ll_icon-services{
          background-color:$LL-DarkGray;
        }
      }
    }
    &.enabled{
      overflow:scroll;
      & .ll_section{
        background-color:$LL-White;
        z-index:$Layer-Level-4;
        width: 100%;
        box-sizing: border-box;
      }
    }
  }
}

