/* Cross-platform CSS  */

li {
  list-style:none;
}

ul {
  padding:0;
  margin:0;
}

button {
  border:none;
  color: $LL-White;
  text-transform: uppercase;
}


/* Android Related CSS for Android Webview */

::-webkit-scrollbar {
  display:none;
}

::-webkit-scrollbar-track {
  display:none;
}

::-webkit-scrollbar-thumb {
  display:none;
}
::-webkit-scrollbar-thumb:window-inactive {
  display:none;
}