.ll_poi-box{
    background-color: $LL-White;
    @include box-shadow(0 0 10px rgba(0,0,0,0.4));
    bottom:0;
    left:0;
    right:0;
    position:absolute;
    width: auto;
    margin: 0;
    height: auto;
    -webkit-font-smoothing: antialiased;
    font-family: $LLTypography;
    font-weight: $Light-Type;
    &:after{
        content:'';
        height: 0;
        border-top: 15px solid transparent;
        border-right: 20px solid $LL-White;
        border-bottom: 15px solid transparent;
        left: -20px;
        position: absolute;
        top: 345px;
        width: 0;
    }
    .ll_title-bar{
        position:absolute;
        background-color:$LL-White;
        color:$LL-DarkGray;
        & .ll_title-bar-title{
            padding:0;
        }
        & .ll_trigger{
            right: 70px;
            top: 20px;
            position: absolute;
            width: 14px;
            height:14px;
            font-size: $LL-Small-Font;
            color: $LL-Blue;
            &:before{
                @include transform(rotate(90deg));
                transition-duration: 300ms;
                transition-property: all;
                position: absolute;
            }
            &:hover:before,&:active:before,&:before.enabled{
                @include transform(rotate(-90deg));
                position: absolute;
            }
        }
    }
}

.ll_poi-box__wrapper{
    background-color: $LL-Ghost;
}

.ll_poi-info{
    background: $LL-White;
    @include box-shadow(0 -2px 5px rgba(0,0,0,0.2));
    .ll_poi-section:last-child{
        border-bottom: 0;
    }
}

.ll_poi-title{
    font-size: 32px;
    font-weight: $Normal-Type;
    line-height: 1.25;
}

.ll_poi-image{
    max-height: 155px;
    img{
        display: inline-block;
        height: 100%;
        width: 100%;
    }
}

.ll_poi-location,
.ll_poi-security,
.ll_poi-level{
    display: block;
    font-size: $LL-Normal-Font;
    line-height: 22px;
    color:$LL-Gray;
    .ll_icon{
        display: inline-block;
        font-size: $LL-Tiny-Font;
        margin-right: 15px;
    }
}

.ll_poi-location,
.ll_poi-security,
.ll_poi-level,
.ll_poi-title {
    max-width: 80%;
    @include ellipsis();
}

.media__body,.ll_poi-navigate-text{
    font-weight:$Normal-Type;
}

.ll_poi-description{
    font-size: $LL-Medium-Font;
    margin:0 0 15px;
}

.ll_poi-box__carousel{
    overflow-x:hidden;
    height: 0;
    width: 100%;
    padding-bottom: 60%;
    position: relative;
    & img{
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -webkit-user-drag: none;
        user-drag: none;
        -webkit-touch-callout: none;
        width: 100%;
        position: absolute;
        height: 100%;
        @include transition-property(left);
        // This creates a incrementing left attribute value for each additional child element.
        // This requires a variable attribute to increment left value.
        $var: 0%;
        @for $i from 1 through 10 {
            &:nth-child(#{$i}) {
                left: #{$var};
                $var: $var + 100%;
            }
        }
    }
    &.disabled{
        position: relative;
        & i{
            width: 32px;
            position: absolute;
            opacity:0.2;
            left:48%; //Legacy Support
            left: calc(50% - 16px);
            right: 0;
            bottom: 50%;
        }
    }
    & .ll_poi-box_photo-status{
        position: absolute;
        top: 51%;
        width: 300px;
        left: 33%; //Legacy Support
        left: calc(50% - 150px);
        & span{
            display:block;
            text-align:center;
            font-weight:$Normal-Type;
            font-size:$LL-Normal-Font;
        }
    }
    .ll_poi-box__carousel-tutorial {
        position: absolute;
        width: 100px;
        height: 100px;
        top: 33%; //Legacy Support
        top: calc(50% - 50px);
        right: 33%; //Legacy Support
        right: calc(50% - 50px);
        background-color: $LL-Black;
        color: $LL-White;
        font-size:$LL-Small-Font;
        text-align: center;
        padding: 12px;
        border-radius: 4px;
        font-weight: $Normal-Type;
        opacity: 0;
        pointer-events: none;
        @include transition-duration(2s);
        @include transition-property(opacity);
        & i{
            height:70px;
            display:block;
            animation: tilt 2.5s infinite;
        }
        &.disabled{
            opacity:0;
        }
    }
}

@keyframes tilt { //Move to Animations
    0% {
        transform: translateX(0px)rotate(0deg);
    }
    20% {
        transform: translateX(-25px)rotate(-6deg);
    }
    40% {
        transform: translateX(0px)rotate(0deg);
    }
    60% {
        transform: translateX(-25px)rotate(-6deg);
    }
    80% {
        transform: translateX(0px)rotate(0deg);
    }
}
.ll_poi-section{
    border-bottom: 1px solid $LL-Ghost;
    padding: 16px;
    .ll_poi-section__field{
        margin-bottom: 10px;
        &:last-child{
            margin-bottom: 0;
        }
    }
    &.ll_poi-section--additional-attribute{
        text-align:center;
    }
    & .ll_operating-hours-block{
        font-weight:$Normal-Type;
        display:inline-block;
        width:50%;
        margin:0 -3px 8px 0;
        & .ll_operating-days{
            font-weight:$Thin-Type;
            font-size:$LL-Normal-Font;
            color:$LL-Gray;
            margin:8px 0 4px 0;
        }
        & .ll_operating-hours{
            color:$LL-DarkGray;
            font-size: $LL-Large-Font;
            font-variant: small-caps;
        }
    }
    & .ll_operating-hours-block.disabled,.ll_operating-hours-block.active{
        margin: 0 0 0 44px;
        display: block;
        width: auto;
    }
}

.ll_poi-section__url{
    & > small{
        display: inline-block;
        font-size: $LL-Normal-Font;
        vertical-align: middle;
        width: 85%;
        @include ellipsis();
    }
}

.ll_poi-section--navigate{
    position: relative;
    height:24px;
}

.ll_poi-navigate{
    position: absolute;
    right: 25px;
    text-align: center;
    top: -30px;
}

.ll_poi-navigate-button {
    background: $LL-Blue;
    border-radius: 50%;
    @include box-shadow(-2px 0px 4px rgba(0,0,0,0.3));
    color: $LL-White;
    cursor: pointer;
    display: block;
    font-size: $LL-Huge-Font;
    height: 55px;
    line-height: 50px !important;
    margin: 0 auto;
    text-align: center;
    width: 55px;
}

.ll_poi-extra-button {
    @extend .ll_poi-navigate-button;

    background: white;
    img {
        width: 25px;
        height: 25px;
        margin: 15px;
    }

}

.ll_poi-navigate-text{
    color: $LL-Blue;
    display: inline-block;
    font-size: $LL-Tiny-Font;
    line-height: 12px;
    width: auto;
}

.ll_poi-icon{
    font-size: 40px;
}

.ll_poi-additional-attributes{
    height: 16px;
    width:auto;
    background: $LL-Blue;
    font-weight:$Medium-Type;
    font-size:$LL-Small-Font;
    border-radius: 2px;
    padding: 10px 16px;
    display: inline-block;
    & span{
        display:inline-block;
        margin-left:8px;
        & a {
            color: $LL-White;
            text-decoration: none;
        }
    }
    & i {
        height: 16px;
        width: 18px;
        font-size: 16px;
        &:before{
            height: 16px;
            width: 18px;
            display: inline-block;
        }
    }
}


.ll_poi-box__carousel-counter{
    position: absolute;
    color: $LL-White;
    background-color: $LL-Black;
    font-size: $LL-Tiny-Font;
    font-weight: $Normal-Type;
    right: 8px;
    top: 8px;
    display: block;
    border-radius: 3px;
    padding: 5px;
    opacity: 0.8;
}

.ll_poi-taglist{
    margin: 0;
    padding: 0;
    text-align: center;
    li{
        background: $LL-Blue;
        border-radius: 2px;
        color: $LL-White;
        display: inline-block;
        font-size: $LL-Small-Font;
        font-weight:$Normal-Type;
        margin-right: 5px;
        margin-bottom: 5px;
        padding: 2px 6px;
    }
}

.ll_poi-section__field{
    label{
        font-size: $LL-Normal-Font;
    }
    p{
        color: $LL-DarkGray;
        font-size: $LL-Large-Font;
        margin: 0;
    }
}

.ll_poi-section__phone{
    font-size: $LL-Large-Font;
}


.ll_poi-section-icon-item{
    color: $LL-Blue;
    font-weight:$Normal-Type;
    display: block;
    margin-bottom:15px;
    text-decoration: none;
    i{
        display: inline-block;
        margin-right: 24px;
        vertical-align: middle;
    }
    &:last-child{
        margin-top: 0;
    }
}