// Non-Global computed styles must be contained inside that which the JavaScript SDK has ownership.
[name="locuslabs-js-sdk-container"] {

  @import "base";

  @import "helpers";
  @import "buttons";
  @import "tooltip";

  @import "dialog";
  @import "level-selector";
  @import "navigation-box";
  @import "poi-box";
  @import "card";
  @import "search";
  @import "directions";
  @import "title-bar";
  @import "bottom-bar";
  @import "overylays";
  @import "location-selector";

  @import "ie-hacks";
}