@media all and (-ms-high-contrast:none) {
  div#ll_location_selector{
    & i#ll_icon-location::before {
      display: block;
      background-size: contain;
      background-repeat: no-repeat;
      background-position-x: 50%;
      background-image: url("../images/map-icon-pin-nav-start.png");
      height: 100%;
      width: 100%;
    }
  }
}