div#ll_location_selector{
  & i#ll_icon-location{
    pointer-events: none;
    width: 44px;
    height: 64px;
    position: absolute;
    left: calc(50% - 22px);
    top: calc(50% - 64px);
    @include animation(bounce 1s ease 0s 1 normal both running);
    &::before{
      content: url('data:image/svg+xml; utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 31 46"><defs><linearGradient id="linear-gradient" x1="15.74" y1="48" x2="15.74" y2="2" gradientTransform="matrix(1, 0, 0, -1, 0, 48)" gradientUnits="userSpaceOnUse"><stop offset="0" stop-color="#487fc0"/><stop offset="1" stop-color="#30489c"/></linearGradient></defs><title>Asset 1</title><g id="Layer_2" data-name="Layer 2"><g id="Layer_1-2" data-name="Layer 1"><path d="M31,15A16,16,0,0,0,16,0,16,16,0,0,0,0,15c0,8,16,31,16,31S31,23,31,15Z" style="fill:url(#linear-gradient)"/><ellipse cx="16" cy="15" rx="12" ry="12" style="fill:#243e82"/><path d="M11,10V20h1l8-5h0L12,9H11Z" style="fill:#fff"/></g></g></svg>');
    }
  }
  button#ll_location_cancel_button, button#ll_location_submit_button{
    @include whmp(50%,48px,0,0);
    display:block;
    position:fixed;
    bottom:0;
    text-align: center;
    z-index:$Layer-Level-10;
    cursor: pointer;
    @include transition(background-color 300ms ease-in-out);
    @extend .ll_standard-typography;
  }
  button#ll_location_cancel_button{
    background-color: $LL-DarkGray;
    left:0;
    &:hover{
     background-color:lighten($LL-DarkGray, 20);
    }
  }
  button#ll_location_submit_button{
    background-color: $LL-Blue;
    right:0;
    &:hover{
      background-color:lighten($LL-Blue, 20);
    }
  }
}

@include keyframes(bounce) {
  0% {
    opacity: 0;
    @include filter(opacity(0));
    @include transform(translateY(-2000px));
  }
  60% {
    opacity: 1;
    @include filter(none);
    @include transform(translateY(20px));
  }
  80% {
    @include transform(translateY(-10px));
  }
  100% {
    @include transform(translateY(0));
  }
}