.ll_button{
  cursor: pointer;
  outline: none;
  padding: 0;
  text-align: center;
}

.ll_buttons-bar{
  position: absolute;
  z-index: $Layer-Level-2;
}

.ll_buttons-bar--hidden {
  display: none;
}

.ll_buttons-bar[data-position='left-top']{
  left: 20px;
  top: 20px;
}

.ll_buttons-bar[data-position='right-top']{
  right: 20px;
  top: 20px;
}

.ll_buttons-bar[data-position='right-bottom']{
  bottom: 20px;
  right: 20px;
}

.ll_buttons-bar[data-position='left-bottom']{
  bottom: 20px;
  left: 20px;
}

.ll_buttons-bar[data-orientation='vertical']{
  .ll_button {
    opacity:0.9;
    display: block;
    margin-top: 15px;
  }
}

.ll_buttons-bar[data-orientation='horizontal']{
  .ll_button {
    display: inline-block;
    margin-left: 15px;
  }
}

.ll_button{
  border: 0;
  border-radius: 50%;
  @include box-shadow(0 2px 5px 0 rgba($LL-Black, 0.16),0 2px 10px 0 rgba($LL-Black, 0.12));
  color: $LL-White;
  height: 45px;
  width: 45px;
}

.ll_button-secondary{
  background: rgba($LL-DarkGray, 0.91);
  &:focus{
    background: rgba($LL-DarkGray, 0.91);
  }
}

.ll_button-primary{
  background: $LL-Red;
  &:focus{
    background: $LL-Red;
  }
}

.ll_button-icon {
  position: relative;
  top: -1px;
}

.ll_icon{
  font-size: $LL-Large-Font;
  vertical-align: middle;
}

#ll_logo{
  height:18px;
  width:90px;
  opacity:0.25;
  bottom:8px;
  left:8px;
  position:absolute;
  z-index:$Layer-Level-0;
  i{
    font-size:14px;
  }
}